import { configureStore, createSlice} from '@reduxjs/toolkit'

//
const cart = createSlice({
  name : 'cart',
  initialState : [],
  reducers : {
    addItem(state, action){
      let idx = state.findIndex((a)=>{return a.id === action.payload.id})
      if(idx>=0){
        ++state[idx].amount
      }else{
            state.push(action.payload)
      }
    },
    removeItem(state,action){
      let idx = state.findIndex((a)=>{return a.id === action.payload})
      if(window.confirm(`${state[idx].title}  ?`)){
        state.splice(idx,1);
      }
      
    },
    increse(state,action){
      let idx = state.findIndex((a)=>{return a.id === action.payload})
      state[idx].amount += 1;
    },
    decrese(state,action){
       let idx = state.findIndex((a)=>{return a.id === action.payload})
      if(state[idx].amount <= 1){
         alert('1  .')
         state[idx].amount=1
      }else{
       --state[idx].amount;
      }
    }
  }
})
export let { addItem, removeItem, increse, decrese } = cart.actions;

//6-store
const store = createSlice({
  name : 'store',
  initialState : [
    {imgUrl : '/img/store1.png', name : '  ', address : '  17 19-12', time:' -  /  12 -  9', lastOrder : ' 8 30()', tel : '02-337-9884'},
    {imgUrl : '/img/store2.png', name : '  ', address : '   134', time:' -  /  11 -  9', lastOrder : ' 8 30()', tel : '02-722-7767'},
    {imgUrl : '/img/store3.png', name : '  ', address : '  3 21 1 102', time:' -  /  12 -  9', lastOrder : ' 8(),  8 30()', tel : '02-468-0889'}
  ]
})

export default configureStore({
  reducer: {
    store : store.reducer,
    cart : cart.reducer,
  }
})