import React, { useRef } from 'react'
import { Container, Button, Form, ButtonGroup } from 'react-bootstrap'

const Login = () => {
    const id = useRef();
    const password = useRef();

    function pass() {
        const idIv = id.current.value;
        const passIv = password.current.value;
        const emailRegEx = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/;
        const reg = /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/;

        if (!emailRegEx.test(idIv)) {
            alert('   ');
            id.current.focus();
            return false
        } else {
            if (false === reg.test(passIv)) {
                alert(' 8  , ///   .');
                password.current.focus();
                return false
            } else if (passIv.search(/\s/) != -1) {
                alert('   .');
                password.current.focus();
                return false;
            } else if (/(\w)\1\1\1/.test(passIv)) {
                alert('  4    .');
                password.current.focus();
                return false;
            }
            else {
                alert('')
            }
        }
    }

    return (
        <Container className='loginPage'>
            <h1></h1>

            <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Control type="email" placeholder="()  ." ref={id} />

            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
                <Form.Control type="password" placeholder="  " ref={password} />
            </Form.Group>
            <Button variant="dark" type="submit" onClick={() => { pass() }} >
                
            </Button>

            <ButtonGroup aria-label="Basic example">
                <Button variant="outline-dark"> </Button>
                <Button variant="outline-dark"> </Button>
                <Button variant="outline-dark"></Button>
            </ButtonGroup>

        </Container>
    )
}

export default Login