import React, {useEffect, useState, useRef} from 'react'
import {Nav, Navbar} from 'react-bootstrap';
import {useNavigate, useLocation} from 'react-router-dom';


const Header = () => {

  const navigate = useNavigate();
  const navBar =useRef();
  const location = useLocation();
 
  const [scrollPosition, setScrollPosition] = useState(0);
  const updateScroll = ()=>{
    setScrollPosition(window.scrollY || document.documentElement.scrollTop);
  };

  useEffect(()=>{
    //    
    window.addEventListener('scroll', updateScroll);

    //    
    if(location.pathname.indexOf('detail')>0 ||location.pathname.indexOf('cart')>0||location.pathname.indexOf('login')>0){
      navBar.current.style.background='black';
    }
   
  });

  return (
    <Navbar collapseOnSelect expand="lg" ref={navBar} className={scrollPosition > 850? 'navBg' :'navBgg'} fixed='top' 
     >
      <Navbar.Brand onClick={()=>{navigate('/')}}><img src={process.env.PUBLIC_URL + "/img/wlogo2.png"} alt="" width={'100px'}/></Navbar.Brand>
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="me-auto">
          <Nav.Link onClick={()=>{navigate('/detail/0')}}></Nav.Link>
          <Nav.Link onClick={()=>{navigate('/')}} href="#Seoul"></Nav.Link>
          <Nav.Link onClick={()=>{navigate('/')}} href="#Motor"></Nav.Link>
          <Nav.Link onClick={()=>{navigate('/')}} href="#Surf"></Nav.Link>
          <Nav.Link></Nav.Link>
          <Nav.Link onClick={()=>{navigate('/')}} href="#journal"></Nav.Link>
        </Nav>
        <Nav>
          <Nav.Link onClick={()=>{navigate('/login')}}><img src={process.env.PUBLIC_URL + "/img/person.png"} alt="" width={'25px'} height={'25px'} /></Nav.Link>
          <Nav.Link eventKey={2} onClick={()=>{navigate('/cart')}}>
          <img src={process.env.PUBLIC_URL + "/img/cart.png"} alt="" width={'25px'} height={'25px'}/>
          </Nav.Link>
        </Nav>
      </Navbar.Collapse>
    
  </Navbar>
  )
}

export default Header